export interface Grade {
  grade: string
  description: string
  minPercentage: number
  maxPercentage: number
}

export interface Student {
  id: string
  name: string
  form: number
  class: string
}

export interface Subject {
  code: string
  name: string
}

export interface ExamResult {
  id: string
  studentId: string
  examId: string
  subject: string
  marks: number
  maxMarks: number
  percentage: number
  grade: string
  description: string
  examType: 'Ujian Pengesanan' | 'UPSA' | 'UASA'
  form: number
  class: string
}

export const SUBJECTS = [
  { code: 'BM', name: 'Bahasa Melayu' },
  { code: 'BI', name: 'Bahasa Inggeris' },
  { code: 'MATE', name: 'Matematik' },
  { code: 'SC', name: 'Sains' },
  { code: 'SEJ', name: 'Sejarah' },
  { code: 'GEO', name: 'Geografi' },
  { code: 'RBT', name: 'Reka Bentuk dan Teknologi' },
  { code: 'PSV', name: 'Pendidikan Seni Visual' },
  { code: 'PJPK', name: 'Pendidikan Jasmani dan Pendidikan Kesihatan' }
]

export const getGradingSystem = (form: number): Grade[] => {
  if (form >= 1 && form <= 3) {
    return [
      { grade: 'A', description: 'Cemerlang', minPercentage: 82, maxPercentage: 100 },
      { grade: 'B', description: 'Kepujian', minPercentage: 66, maxPercentage: 81 },
      { grade: 'C', description: 'Baik', minPercentage: 50, maxPercentage: 65 },
      { grade: 'D', description: 'Memuaskan', minPercentage: 35, maxPercentage: 49 },
      { grade: 'E', description: 'Mencapai tahap minimum', minPercentage: 20, maxPercentage: 34 },
      { grade: 'F', description: 'Belum mencapai tahap minimum', minPercentage: 0, maxPercentage: 19 }
    ]
  } else if (form >= 4 && form <= 5) {
    // SPM grading system based on the image
    return [
      { grade: 'A+', description: 'Cemerlang Tertinggi', minPercentage: 90, maxPercentage: 100 },
      { grade: 'A', description: 'Cemerlang Tinggi', minPercentage: 80, maxPercentage: 89 },
      { grade: 'A-', description: 'Cemerlang', minPercentage: 70, maxPercentage: 79 },
      { grade: 'B+', description: 'Kepujian Tertinggi', minPercentage: 65, maxPercentage: 69 },
      { grade: 'B', description: 'Kepujian Tinggi', minPercentage: 60, maxPercentage: 64 },
      { grade: 'C+', description: 'Kepujian Atas', minPercentage: 55, maxPercentage: 59 },
      { grade: 'C', description: 'Kepujian', minPercentage: 50, maxPercentage: 54 },
      { grade: 'D', description: 'Lulus Atas', minPercentage: 45, maxPercentage: 49 },
      { grade: 'E', description: 'Lulus', minPercentage: 40, maxPercentage: 44 },
      { grade: 'G', description: 'Gagal', minPercentage: 0, maxPercentage: 39 }
    ]
  }
  return []
}

export const calculateGrade = (percentage: number, form: number): { grade: string; description: string } => {
  const gradingSystem = getGradingSystem(form)
  const grade = gradingSystem.find(g => percentage >= g.minPercentage && percentage <= g.maxPercentage)
  
  if (grade) {
    return { grade: grade.grade, description: grade.description }
  }
  
  return { grade: 'F', description: 'Belum mencapai tahap minimum' }
}

export const calculatePercentage = (marks: number, maxMarks: number): number => {
  return Math.round((marks / maxMarks) * 100)
}
