"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { ExamResult, SUBJECTS, getGradingSystem } from '@/lib/grading'
import jsPDF from 'jspdf'
import html2canvas from 'html2canvas'

interface AnalysisData {
  totalStudents: number
  averagePercentage: number
  gradeDistribution: { [key: string]: number }
  subjectAnalysis: { [key: string]: { average: number; count: number } }
  classAnalysis: { [key: string]: { average: number; count: number } }
  formAnalysis: { [key: string]: { average: number; count: number } }
}

export default function AnalysisDashboard() {
  const [examResults, setExamResults] = useState<ExamResult[]>([])
  const [selectedForm, setSelectedForm] = useState<string>('all')
  const [selectedClass, setSelectedClass] = useState<string>('all')
  const [selectedSubject, setSelectedSubject] = useState<string>('all')
  const [selectedExamType, setSelectedExamType] = useState<string>('all')
  const [analysisData, setAnalysisData] = useState<AnalysisData | null>(null)
  const [isGeneratingPDF, setIsGeneratingPDF] = useState(false)

  useEffect(() => {
    const savedResults = localStorage.getItem('examResults')
    if (savedResults) {
      setExamResults(JSON.parse(savedResults))
    }
  }, [])

  useEffect(() => {
    if (examResults.length > 0) {
      generateAnalysis()
    }
  }, [examResults, selectedForm, selectedClass, selectedSubject, selectedExamType])

  const generateAnalysis = () => {
    let filteredResults = examResults

    if (selectedForm !== 'all') {
      filteredResults = filteredResults.filter(result => result.form.toString() === selectedForm)
    }
    if (selectedClass !== 'all') {
      filteredResults = filteredResults.filter(result => result.class === selectedClass)
    }
    if (selectedSubject !== 'all') {
      filteredResults = filteredResults.filter(result => result.subject === selectedSubject)
    }
    if (selectedExamType !== 'all') {
      filteredResults = filteredResults.filter(result => result.examType === selectedExamType)
    }

    if (filteredResults.length === 0) {
      setAnalysisData(null)
      return
    }

    const totalStudents = new Set(filteredResults.map(r => r.studentId)).size
    const averagePercentage = filteredResults.reduce((sum, r) => sum + r.percentage, 0) / filteredResults.length

    const gradeDistribution: { [key: string]: number } = {}
    filteredResults.forEach(result => {
      gradeDistribution[result.grade] = (gradeDistribution[result.grade] || 0) + 1
    })

    const subjectAnalysis: { [key: string]: { average: number; count: number } } = {}
    filteredResults.forEach(result => {
      if (!subjectAnalysis[result.subject]) {
        subjectAnalysis[result.subject] = { average: 0, count: 0 }
      }
      subjectAnalysis[result.subject].average += result.percentage
      subjectAnalysis[result.subject].count += 1
    })
    Object.keys(subjectAnalysis).forEach(subject => {
      subjectAnalysis[subject].average = subjectAnalysis[subject].average / subjectAnalysis[subject].count
    })

    const classAnalysis: { [key: string]: { average: number; count: number } } = {}
    filteredResults.forEach(result => {
      if (!classAnalysis[result.class]) {
        classAnalysis[result.class] = { average: 0, count: 0 }
      }
      classAnalysis[result.class].average += result.percentage
      classAnalysis[result.class].count += 1
    })
    Object.keys(classAnalysis).forEach(cls => {
      classAnalysis[cls].average = classAnalysis[cls].average / classAnalysis[cls].count
    })

    const formAnalysis: { [key: string]: { average: number; count: number } } = {}
    filteredResults.forEach(result => {
      const form = result.form.toString()
      if (!formAnalysis[form]) {
        formAnalysis[form] = { average: 0, count: 0 }
      }
      formAnalysis[form].average += result.percentage
      formAnalysis[form].count += 1
    })
    Object.keys(formAnalysis).forEach(form => {
      formAnalysis[form].average = formAnalysis[form].average / formAnalysis[form].count
    })

    setAnalysisData({
      totalStudents,
      averagePercentage,
      gradeDistribution,
      subjectAnalysis,
      classAnalysis,
      formAnalysis
    })
  }

  const getUniqueClasses = () => {
    return [...new Set(examResults.map(result => result.class))]
  }

  const getGradeColor = (grade: string) => {
    const colors: { [key: string]: string } = {
      'A+': 'bg-green-600',
      'A': 'bg-green-500',
      'A-': 'bg-green-400',
      'B+': 'bg-blue-500',
      'B': 'bg-blue-400',
      'C+': 'bg-yellow-500',
      'C': 'bg-yellow-400',
      'D': 'bg-orange-400',
      'E': 'bg-red-400',
      'F': 'bg-red-600',
      'G': 'bg-gray-600'
    }
    return colors[grade] || 'bg-gray-400'
  }

  const handleDownloadPDF = async () => {
    if (!analysisData) {
      alert('Tiada data analisis untuk dicetak. Sila masukkan markah pelajar terlebih dahulu.')
      return
    }

    setIsGeneratingPDF(true)
    
    try {
      const element = document.getElementById('analysisContent')
      if (!element) {
        throw new Error('Kandungan analisis tidak dijumpai')
      }

      // Capture the element as canvas
      const canvas = await html2canvas(element, {
        scale: 2,
        useCORS: true,
        allowTaint: true,
        backgroundColor: '#ffffff'
      })

      // Create PDF
      const imgData = canvas.toDataURL('image/png')
      const pdf = new jsPDF('p', 'mm', 'a4')
      
      const imgWidth = 210 // A4 width in mm
      const pageHeight = 295 // A4 height in mm
      const imgHeight = (canvas.height * imgWidth) / canvas.width
      let heightLeft = imgHeight

      let position = 0

      // Add first page
      pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight)
      heightLeft -= pageHeight

      // Add additional pages if needed
      while (heightLeft >= 0) {
        position = heightLeft - imgHeight
        pdf.addPage()
        pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight)
        heightLeft -= pageHeight
      }

      // Generate filename with current date
      const currentDate = new Date().toLocaleDateString('ms-MY').replace(/\//g, '-')
      const filename = `Analisis_Keputusan_${currentDate}.pdf`

      // Download the PDF
      pdf.save(filename)
      
    } catch (error) {
      console.error('Ralat semasa menjana PDF:', error)
      alert('Ralat berlaku semasa menjana PDF. Sila cuba lagi.')
    } finally {
      setIsGeneratingPDF(false)
    }
  }

  if (!analysisData) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Analisis Keputusan Peperiksaan</CardTitle>
          <CardDescription>Tiada data untuk dianalisis. Sila masukkan markah pelajar terlebih dahulu.</CardDescription>
        </CardHeader>
      </Card>
    )
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex justify-between items-start">
            <div>
              <CardTitle>Analisis Keputusan Peperiksaan</CardTitle>
              <CardDescription>Analisis komprehensif prestasi pelajar</CardDescription>
            </div>
            <Button 
              onClick={handleDownloadPDF}
              disabled={!analysisData || isGeneratingPDF}
              className="bg-green-600 hover:bg-green-700"
            >
              {isGeneratingPDF ? 'Menjana PDF...' : 'Cetak PDF Analisis'}
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          <div id="analysisContent">
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
            <Select value={selectedForm} onValueChange={setSelectedForm}>
              <SelectTrigger>
                <SelectValue placeholder="Pilih Tingkatan" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Semua Tingkatan</SelectItem>
                {[1, 2, 3, 4, 5].map(form => (
                  <SelectItem key={form} value={form.toString()}>Tingkatan {form}</SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select value={selectedClass} onValueChange={setSelectedClass}>
              <SelectTrigger>
                <SelectValue placeholder="Pilih Kelas" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Semua Kelas</SelectItem>
                {getUniqueClasses().map(cls => (
                  <SelectItem key={cls} value={cls}>{cls}</SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select value={selectedSubject} onValueChange={setSelectedSubject}>
              <SelectTrigger>
                <SelectValue placeholder="Pilih Subjek" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Semua Subjek</SelectItem>
                {SUBJECTS.map(subject => (
                  <SelectItem key={subject.code} value={subject.code}>{subject.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select value={selectedExamType} onValueChange={setSelectedExamType}>
              <SelectTrigger>
                <SelectValue placeholder="Jenis Ujian" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Semua Ujian</SelectItem>
                <SelectItem value="Ujian Pengesanan">Ujian Pengesanan</SelectItem>
                <SelectItem value="UPSA">UPSA</SelectItem>
                <SelectItem value="UASA">UASA</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <Tabs defaultValue="overview" className="w-full">
            <TabsList className="grid w-full grid-cols-4">
              <TabsTrigger value="overview">Ringkasan</TabsTrigger>
              <TabsTrigger value="subjects">Subjek</TabsTrigger>
              <TabsTrigger value="classes">Kelas</TabsTrigger>
              <TabsTrigger value="forms">Tingkatan</TabsTrigger>
            </TabsList>

            <TabsContent value="overview" className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-sm font-medium">Jumlah Pelajar</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold">{analysisData.totalStudents}</div>
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-sm font-medium">Purata Keseluruhan</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold">{analysisData.averagePercentage.toFixed(1)}%</div>
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-sm font-medium">Taburan Gred</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="flex flex-wrap gap-2">
                      {Object.entries(analysisData.gradeDistribution).map(([grade, count]) => (
                        <Badge key={grade} className={`${getGradeColor(grade)} text-white`}>
                          {grade}: {count}
                        </Badge>
                      ))}
                    </div>
                  </CardContent>
                </Card>
              </div>
            </TabsContent>

            <TabsContent value="subjects">
              <Card>
                <CardHeader>
                  <CardTitle>Analisis Mengikut Subjek</CardTitle>
                </CardHeader>
                <CardContent>
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>Subjek</TableHead>
                        <TableHead>Purata (%)</TableHead>
                        <TableHead>Bilangan Pelajar</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {Object.entries(analysisData.subjectAnalysis).map(([subject, data]) => {
                        const subjectName = SUBJECTS.find(s => s.code === subject)?.name || subject
                        return (
                          <TableRow key={subject}>
                            <TableCell className="font-medium">{subjectName}</TableCell>
                            <TableCell>{data.average.toFixed(1)}%</TableCell>
                            <TableCell>{data.count}</TableCell>
                          </TableRow>
                        )
                      })}
                    </TableBody>
                  </Table>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="classes">
              <Card>
                <CardHeader>
                  <CardTitle>Analisis Mengikut Kelas</CardTitle>
                </CardHeader>
                <CardContent>
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>Kelas</TableHead>
                        <TableHead>Purata (%)</TableHead>
                        <TableHead>Bilangan Keputusan</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {Object.entries(analysisData.classAnalysis).map(([cls, data]) => (
                        <TableRow key={cls}>
                          <TableCell className="font-medium">{cls}</TableCell>
                          <TableCell>{data.average.toFixed(1)}%</TableCell>
                          <TableCell>{data.count}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="forms">
              <Card>
                <CardHeader>
                  <CardTitle>Analisis Mengikut Tingkatan</CardTitle>
                </CardHeader>
                <CardContent>
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>Tingkatan</TableHead>
                        <TableHead>Purata (%)</TableHead>
                        <TableHead>Bilangan Keputusan</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {Object.entries(analysisData.formAnalysis).map(([form, data]) => (
                        <TableRow key={form}>
                          <TableCell className="font-medium">Tingkatan {form}</TableCell>
                          <TableCell>{data.average.toFixed(1)}%</TableCell>
                          <TableCell>{data.count}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
