"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { SUBJECTS } from '@/lib/grading'

interface Exam {
  id: string
  name: string
  type: 'Ujian Pengesanan' | 'UPSA' | 'UASA'
  maxMarks: number
  form: number
  subjects: string[]
  date: string
}

export default function ExamManagement() {
  const [exams, setExams] = useState<Exam[]>([])
  const [newExam, setNewExam] = useState<Exam>({
    id: '',
    name: '',
    type: 'Ujian Pengesanan',
    maxMarks: 100,
    form: 1,
    subjects: [],
    date: new Date().toISOString().split('T')[0]
  })
  const [editingId, setEditingId] = useState<string | null>(null)

  useEffect(() => {
    const savedExams = localStorage.getItem('exams')
    if (savedExams) {
      setExams(JSON.parse(savedExams))
    }
  }, [])

  useEffect(() => {
    localStorage.setItem('exams', JSON.stringify(exams))
  }, [exams])

  const handleAddExam = () => {
    if (newExam.name && newExam.subjects.length > 0) {
      const exam: Exam = {
        ...newExam,
        id: Date.now().toString()
      }
      setExams([...exams, exam])
      setNewExam({
        id: '',
        name: '',
        type: 'Ujian Pengesanan',
        maxMarks: 100,
        form: 1,
        subjects: [],
        date: new Date().toISOString().split('T')[0]
      })
    }
  }

  const handleDeleteExam = (id: string) => {
    setExams(exams.filter(exam => exam.id !== id))
  }

  const handleEditExam = (exam: Exam) => {
    setEditingId(exam.id)
    setNewExam(exam)
  }

  const handleUpdateExam = () => {
    if (editingId && newExam.name && newExam.subjects.length > 0) {
      setExams(exams.map(exam => 
        exam.id === editingId ? { ...newExam, id: editingId } : exam
      ))
      setEditingId(null)
      setNewExam({
        id: '',
        name: '',
        type: 'Ujian Pengesanan',
        maxMarks: 100,
        form: 1,
        subjects: [],
        date: new Date().toISOString().split('T')[0]
      })
    }
  }

  const toggleSubject = (subjectCode: string) => {
    setNewExam(prev => ({
      ...prev,
      subjects: prev.subjects.includes(subjectCode)
        ? prev.subjects.filter(s => s !== subjectCode)
        : [...prev.subjects, subjectCode]
    }))
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Pengurusan Peperiksaan</CardTitle>
          <CardDescription>Tambah dan urus maklumat peperiksaan</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
            <div>
              <Label htmlFor="name">Nama Peperiksaan</Label>
              <Input
                id="name"
                value={newExam.name}
                onChange={(e) => setNewExam({ ...newExam, name: e.target.value })}
                placeholder="Contoh: Ujian Pengesanan 1"
              />
            </div>
            <div>
              <Label htmlFor="type">Jenis Peperiksaan</Label>
              <Select 
                value={newExam.type} 
                onValueChange={(value) => setNewExam({ ...newExam, type: value as any })}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="Ujian Pengesanan">Ujian Pengesanan</SelectItem>
                  <SelectItem value="UPSA">UPSA</SelectItem>
                  <SelectItem value="UASA">UASA</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div>
              <Label htmlFor="form">Tingkatan</Label>
              <Select 
                value={newExam.form.toString()} 
                onValueChange={(value) => setNewExam({ ...newExam, form: parseInt(value) })}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {[1, 2, 3, 4, 5].map(form => (
                    <SelectItem key={form} value={form.toString()}>Tingkatan {form}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div>
              <Label htmlFor="maxMarks">Markah Maksimum</Label>
              <Input
                id="maxMarks"
                type="number"
                value={newExam.maxMarks}
                onChange={(e) => setNewExam({ ...newExam, maxMarks: parseInt(e.target.value) || 100 })}
                min="1"
                max="100"
              />
            </div>
            <div>
              <Label htmlFor="date">Tarikh</Label>
              <Input
                id="date"
                type="date"
                value={newExam.date}
                onChange={(e) => setNewExam({ ...newExam, date: e.target.value })}
              />
            </div>
          </div>

          <div className="mb-4">
            <Label>Subjek</Label>
            <div className="grid grid-cols-2 md:grid-cols-3 gap-2 mt-2">
              {SUBJECTS.map(subject => (
                <div key={subject.code} className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    id={`subject-${subject.code}`}
                    checked={newExam.subjects.includes(subject.code)}
                    onChange={() => toggleSubject(subject.code)}
                  />
                  <label htmlFor={`subject-${subject.code}`} className="text-sm">
                    {subject.name}
                  </label>
                </div>
              ))}
            </div>
          </div>

          <div className="flex gap-2">
            {editingId ? (
              <>
                <Button onClick={handleUpdateExam}>Kemas Kini</Button>
                <Button variant="outline" onClick={() => {
                  setEditingId(null)
                  setNewExam({
                    id: '',
                    name: '',
                    type: 'Ujian Pengesanan',
                    maxMarks: 100,
                    form: 1,
                    subjects: [],
                    date: new Date().toISOString().split('T')[0]
                  })
                }}>Batal</Button>
              </>
            ) : (
              <Button onClick={handleAddExam}>Tambah Peperiksaan</Button>
            )}
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Senarai Peperiksaan</CardTitle>
          <CardDescription>Jumlah peperiksaan: {exams.length}</CardDescription>
        </CardHeader>
        <CardContent>
          {exams.length === 0 ? (
            <p className="text-center text-gray-500 py-8">Tiada peperiksaan didaftarkan</p>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Nama</TableHead>
                  <TableHead>Jenis</TableHead>
                  <TableHead>Tingkatan</TableHead>
                  <TableHead>Markah Maks</TableHead>
                  <TableHead>Subjek</TableHead>
                  <TableHead>Tarikh</TableHead>
                  <TableHead>Tindakan</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {exams.map(exam => (
                  <TableRow key={exam.id}>
                    <TableCell className="font-medium">{exam.name}</TableCell>
                    <TableCell>
                      <Badge variant="outline">{exam.type}</Badge>
                    </TableCell>
                    <TableCell>Tingkatan {exam.form}</TableCell>
                    <TableCell>{exam.maxMarks}</TableCell>
                    <TableCell>
                      <div className="flex flex-wrap gap-1">
                        {exam.subjects.map(subject => (
                          <Badge key={subject} variant="secondary" className="text-xs">
                            {subject}
                          </Badge>
                        ))}
                      </div>
                    </TableCell>
                    <TableCell>{new Date(exam.date).toLocaleDateString('ms-MY')}</TableCell>
                    <TableCell>
                      <div className="flex gap-2">
                        <Button 
                          size="sm" 
                          variant="outline"
                          onClick={() => handleEditExam(exam)}
                        >
                          Edit
                        </Button>
                        <Button 
                          size="sm" 
                          variant="destructive"
                          onClick={() => handleDeleteExam(exam.id)}
                        >
                          Padam
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
