"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Student } from '@/lib/grading'

export default function StudentManagement() {
  const [students, setStudents] = useState<Student[]>([])
  const [newStudent, setNewStudent] = useState({ name: '', form: 1, class: '' })
  const [editingId, setEditingId] = useState<string | null>(null)

  useEffect(() => {
    const savedStudents = localStorage.getItem('students')
    if (savedStudents) {
      setStudents(JSON.parse(savedStudents))
    }
  }, [])

  useEffect(() => {
    localStorage.setItem('students', JSON.stringify(students))
  }, [students])

  const handleAddStudent = () => {
    if (newStudent.name && newStudent.class) {
      const student: Student = {
        id: Date.now().toString(),
        name: newStudent.name,
        form: newStudent.form,
        class: newStudent.class
      }
      setStudents([...students, student])
      setNewStudent({ name: '', form: 1, class: '' })
    }
  }

  const handleDeleteStudent = (id: string) => {
    setStudents(students.filter(student => student.id !== id))
  }

  const handleEditStudent = (student: Student) => {
    setEditingId(student.id)
    setNewStudent({ name: student.name, form: student.form, class: student.class })
  }

  const handleUpdateStudent = () => {
    if (editingId && newStudent.name && newStudent.class) {
      setStudents(students.map(student => 
        student.id === editingId 
          ? { ...student, name: newStudent.name, form: newStudent.form, class: newStudent.class }
          : student
      ))
      setEditingId(null)
      setNewStudent({ name: '', form: 1, class: '' })
    }
  }

  const getUniqueClasses = () => {
    return [...new Set(students.map(student => student.class))]
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Pengurusan Pelajar</CardTitle>
          <CardDescription>Tambah dan urus maklumat pelajar</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
            <div>
              <Label htmlFor="name">Nama Pelajar</Label>
              <Input
                id="name"
                value={newStudent.name}
                onChange={(e) => setNewStudent({ ...newStudent, name: e.target.value })}
                placeholder="Masukkan nama pelajar"
              />
            </div>
            <div>
              <Label htmlFor="form">Tingkatan</Label>
              <Select 
                value={newStudent.form.toString()} 
                onValueChange={(value) => setNewStudent({ ...newStudent, form: parseInt(value) })}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {[1, 2, 3, 4, 5].map(form => (
                    <SelectItem key={form} value={form.toString()}>Tingkatan {form}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div>
              <Label htmlFor="class">Kelas</Label>
              <Input
                id="class"
                value={newStudent.class}
                onChange={(e) => setNewStudent({ ...newStudent, class: e.target.value })}
                placeholder="Contoh: 1A, 2B, 3C"
              />
            </div>
          </div>
          <div className="flex gap-2">
            {editingId ? (
              <>
                <Button onClick={handleUpdateStudent}>Kemas Kini</Button>
                <Button variant="outline" onClick={() => {
                  setEditingId(null)
                  setNewStudent({ name: '', form: 1, class: '' })
                }}>Batal</Button>
              </>
            ) : (
              <Button onClick={handleAddStudent}>Tambah Pelajar</Button>
            )}
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Senarai Pelajar</CardTitle>
          <CardDescription>Jumlah pelajar: {students.length}</CardDescription>
        </CardHeader>
        <CardContent>
          {students.length === 0 ? (
            <p className="text-center text-gray-500 py-8">Tiada pelajar didaftarkan</p>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Nama</TableHead>
                  <TableHead>Tingkatan</TableHead>
                  <TableHead>Kelas</TableHead>
                  <TableHead>Tindakan</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {students.map(student => (
                  <TableRow key={student.id}>
                    <TableCell className="font-medium">{student.name}</TableCell>
                    <TableCell>Tingkatan {student.form}</TableCell>
                    <TableCell>{student.class}</TableCell>
                    <TableCell>
                      <div className="flex gap-2">
                        <Button 
                          size="sm" 
                          variant="outline"
                          onClick={() => handleEditStudent(student)}
                        >
                          Edit
                        </Button>
                        <Button 
                          size="sm" 
                          variant="destructive"
                          onClick={() => handleDeleteStudent(student.id)}
                        >
                          Padam
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
