"use client"

import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Separator } from '@/components/ui/separator'
import { ExamResult, SUBJECTS } from '@/lib/grading'

interface ResultSlipProps {
  examResult: ExamResult
  studentName: string
  examName: string
  onClose?: () => void
}

export default function ResultSlip({ examResult, studentName, examName, onClose }: ResultSlipProps) {
  const handlePrint = () => {
    window.print()
  }

  const getSubjectName = (subjectCode: string) => {
    return SUBJECTS.find(s => s.code === subjectCode)?.name || subjectCode
  }

  const getGradeColor = (grade: string) => {
    const colors: { [key: string]: string } = {
      'A+': 'text-green-600',
      'A': 'text-green-500',
      'A-': 'text-green-400',
      'B+': 'text-blue-500',
      'B': 'text-blue-400',
      'C+': 'text-yellow-600',
      'C': 'text-yellow-500',
      'D': 'text-orange-500',
      'E': 'text-red-400',
      'F': 'text-red-600',
      'G': 'text-gray-600'
    }
    return colors[grade] || 'text-gray-400'
  }

  if (!examResult) {
    return (
      <Card className="w-full max-w-2xl mx-auto">
        <CardHeader>
          <CardTitle className="text-center text-red-600">Ralat</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-center text-gray-600">Tiada data keputusan untuk dipaparkan.</p>
        </CardContent>
      </Card>
    )
  }

  return (
    <div className="w-full max-w-2xl mx-auto bg-white">
      <div className="print:shadow-none shadow-lg">
        <Card className="border-0 print:border-0">
          <CardHeader className="text-center pb-6">
            <div className="space-y-2">
              <h1 className="text-2xl font-bold text-gray-900">SLIP KEPUTUSAN PEPERIKSAAN</h1>
              <h2 className="text-lg font-semibold text-gray-700">Sekolah Menengah</h2>
              <Separator className="my-4" />
            </div>
          </CardHeader>
          
          <CardContent className="space-y-6">
            {/* Maklumat Pelajar */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-3">
                <div className="flex justify-between">
                  <span className="font-medium text-gray-600">Nama Pelajar:</span>
                  <span className="font-semibold text-gray-900">{studentName}</span>
                </div>
                <div className="flex justify-between">
                  <span className="font-medium text-gray-600">Tingkatan:</span>
                  <span className="font-semibold text-gray-900">Tingkatan {examResult.form}</span>
                </div>
                <div className="flex justify-between">
                  <span className="font-medium text-gray-600">Kelas:</span>
                  <span className="font-semibold text-gray-900">{examResult.class}</span>
                </div>
              </div>
              
              <div className="space-y-3">
                <div className="flex justify-between">
                  <span className="font-medium text-gray-600">Peperiksaan:</span>
                  <span className="font-semibold text-gray-900">{examName}</span>
                </div>
                <div className="flex justify-between">
                  <span className="font-medium text-gray-600">Jenis Ujian:</span>
                  <span className="font-semibold text-gray-900">{examResult.examType}</span>
                </div>
                <div className="flex justify-between">
                  <span className="font-medium text-gray-600">Tarikh:</span>
                  <span className="font-semibold text-gray-900">{new Date().toLocaleDateString('ms-MY')}</span>
                </div>
              </div>
            </div>

            <Separator />

            {/* Keputusan */}
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900 text-center">KEPUTUSAN SUBJEK</h3>
              
              <div className="bg-gray-50 p-6 rounded-lg">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-3">
                    <div className="flex justify-between">
                      <span className="font-medium text-gray-600">Subjek:</span>
                      <span className="font-semibold text-gray-900">{getSubjectName(examResult.subject)}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="font-medium text-gray-600">Markah:</span>
                      <span className="font-semibold text-gray-900">{examResult.marks} / {examResult.maxMarks}</span>
                    </div>
                  </div>
                  
                  <div className="space-y-3">
                    <div className="flex justify-between">
                      <span className="font-medium text-gray-600">Peratus:</span>
                      <span className="font-semibold text-gray-900">{examResult.percentage}%</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="font-medium text-gray-600">Gred:</span>
                      <span className={`font-bold text-xl ${getGradeColor(examResult.grade)}`}>
                        {examResult.grade}
                      </span>
                    </div>
                  </div>
                </div>
                
                <div className="mt-4 text-center">
                  <div className="flex justify-between">
                    <span className="font-medium text-gray-600">Keterangan:</span>
                    <span className="font-semibold text-gray-900">{examResult.description}</span>
                  </div>
                </div>
              </div>
            </div>

            <Separator />

            {/* Tandatangan */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mt-8">
              <div className="text-center space-y-8">
                <div>
                  <div className="border-b border-gray-400 w-48 mx-auto mb-2"></div>
                  <p className="text-sm text-gray-600">Tandatangan Guru Subjek</p>
                </div>
              </div>
              
              <div className="text-center space-y-8">
                <div>
                  <div className="border-b border-gray-400 w-48 mx-auto mb-2"></div>
                  <p className="text-sm text-gray-600">Tandatangan Guru Kelas</p>
                </div>
              </div>
            </div>

            {/* Butang Cetak */}
            <div className="flex justify-center gap-4 mt-8 print:hidden">
              <Button onClick={handlePrint} className="bg-blue-600 hover:bg-blue-700">
                Cetak Slip
              </Button>
              {onClose && (
                <Button variant="outline" onClick={onClose}>
                  Tutup
                </Button>
              )}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
