"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog'
import { Student, ExamResult, SUBJECTS, calculatePercentage, calculateGrade } from '@/lib/grading'
import ResultSlip from './ResultSlip'

interface Exam {
  id: string
  name: string
  type: 'Ujian Pengesanan' | 'UPSA' | 'UASA'
  maxMarks: number
  form: number
  subjects: string[]
}

interface DataEntryForm {
  studentId: string
  examId: string
  subject: string
  marks: number
}

export default function DataEntry() {
  const [students, setStudents] = useState<Student[]>([])
  const [exams, setExams] = useState<Exam[]>([])
  const [examResults, setExamResults] = useState<ExamResult[]>([])
  const [formData, setFormData] = useState<DataEntryForm>({
    studentId: '',
    examId: '',
    subject: '',
    marks: 0
  })
  const [editingId, setEditingId] = useState<string | null>(null)
  const [selectedResult, setSelectedResult] = useState<ExamResult | null>(null)
  const [isSlipModalOpen, setIsSlipModalOpen] = useState(false)

  useEffect(() => {
    const savedStudents = localStorage.getItem('students')
    const savedExams = localStorage.getItem('exams')
    const savedResults = localStorage.getItem('examResults')
    
    if (savedStudents) setStudents(JSON.parse(savedStudents))
    if (savedExams) setExams(JSON.parse(savedExams))
    if (savedResults) setExamResults(JSON.parse(savedResults))
  }, [])

  useEffect(() => {
    localStorage.setItem('examResults', JSON.stringify(examResults))
  }, [examResults])

  const handleAddResult = () => {
    if (formData.studentId && formData.examId && formData.subject && formData.marks >= 0) {
      const student = students.find(s => s.id === formData.studentId)
      const exam = exams.find(e => e.id === formData.examId)
      
      if (student && exam) {
        const percentage = calculatePercentage(formData.marks, exam.maxMarks)
        const gradeInfo = calculateGrade(percentage, student.form)
        
        const result: ExamResult = {
          id: Date.now().toString(),
          studentId: formData.studentId,
          examId: formData.examId,
          subject: formData.subject,
          marks: formData.marks,
          maxMarks: exam.maxMarks,
          percentage,
          grade: gradeInfo.grade,
          description: gradeInfo.description,
          examType: exam.type,
          form: student.form,
          class: student.class
        }
        
        setExamResults([...examResults, result])
        setFormData({ studentId: '', examId: '', subject: '', marks: 0 })
      }
    }
  }

  const handleDeleteResult = (id: string) => {
    setExamResults(examResults.filter(result => result.id !== id))
  }

  const handlePrintSlip = (result: ExamResult) => {
    setSelectedResult(result)
    setIsSlipModalOpen(true)
  }

  const handleCloseSlipModal = () => {
    setIsSlipModalOpen(false)
    setSelectedResult(null)
  }

  const getFilteredExams = () => {
    if (!formData.studentId) return exams
    
    const student = students.find(s => s.id === formData.studentId)
    if (!student) return exams
    
    return exams.filter(exam => exam.form === student.form)
  }

  const getFilteredSubjects = () => {
    if (!formData.examId) return []
    
    const exam = exams.find(e => e.id === formData.examId)
    if (!exam) return []
    
    return exam.subjects
  }

  const getStudentName = (studentId: string) => {
    return students.find(s => s.id === studentId)?.name || 'Unknown'
  }

  const getExamName = (examId: string) => {
    return exams.find(e => e.id === examId)?.name || 'Unknown'
  }

  const getSubjectName = (subjectCode: string) => {
    return SUBJECTS.find(s => s.code === subjectCode)?.name || subjectCode
  }

  const getGradeColor = (grade: string) => {
    const colors: { [key: string]: string } = {
      'A+': 'bg-green-600',
      'A': 'bg-green-500',
      'A-': 'bg-green-400',
      'B+': 'bg-blue-500',
      'B': 'bg-blue-400',
      'C+': 'bg-yellow-500',
      'C': 'bg-yellow-400',
      'D': 'bg-orange-400',
      'E': 'bg-red-400',
      'F': 'bg-red-600',
      'G': 'bg-gray-600'
    }
    return colors[grade] || 'bg-gray-400'
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Masukkan Markah Pelajar</CardTitle>
          <CardDescription>Masukkan keputusan peperiksaan pelajar</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-4">
            <div>
              <Label htmlFor="student">Pelajar</Label>
              <Select 
                value={formData.studentId} 
                onValueChange={(value) => setFormData({ ...formData, studentId: value, examId: '', subject: '' })}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Pilih pelajar" />
                </SelectTrigger>
                <SelectContent>
                  {students.map(student => (
                    <SelectItem key={student.id} value={student.id}>
                      {student.name} - Tingkatan {student.form} {student.class}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            
            <div>
              <Label htmlFor="exam">Peperiksaan</Label>
              <Select 
                value={formData.examId} 
                onValueChange={(value) => setFormData({ ...formData, examId: value, subject: '' })}
                disabled={!formData.studentId}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Pilih peperiksaan" />
                </SelectTrigger>
                <SelectContent>
                  {getFilteredExams().map(exam => (
                    <SelectItem key={exam.id} value={exam.id}>
                      {exam.name} - {exam.type}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            
            <div>
              <Label htmlFor="subject">Subjek</Label>
              <Select 
                value={formData.subject} 
                onValueChange={(value) => setFormData({ ...formData, subject: value })}
                disabled={!formData.examId}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Pilih subjek" />
                </SelectTrigger>
                <SelectContent>
                  {getFilteredSubjects().map(subject => (
                    <SelectItem key={subject} value={subject}>
                      {getSubjectName(subject)}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            
            <div>
              <Label htmlFor="marks">Markah</Label>
              <Input
                id="marks"
                type="number"
                value={formData.marks}
                onChange={(e) => setFormData({ ...formData, marks: parseInt(e.target.value) || 0 })}
                min="0"
                max={formData.examId ? exams.find(e => e.id === formData.examId)?.maxMarks || 100 : 100}
                disabled={!formData.subject}
              />
            </div>
          </div>
          
          <Button 
            onClick={handleAddResult} 
            disabled={!formData.studentId || !formData.examId || !formData.subject}
          >
            Tambah Keputusan
          </Button>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Senarai Keputusan</CardTitle>
          <CardDescription>Jumlah keputusan: {examResults.length}</CardDescription>
        </CardHeader>
        <CardContent>
          {examResults.length === 0 ? (
            <p className="text-center text-gray-500 py-8">Tiada keputusan dimasukkan</p>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Pelajar</TableHead>
                  <TableHead>Peperiksaan</TableHead>
                  <TableHead>Subjek</TableHead>
                  <TableHead>Markah</TableHead>
                  <TableHead>Peratus</TableHead>
                  <TableHead>Gred</TableHead>
                  <TableHead>Tindakan</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {examResults.map(result => (
                  <TableRow key={result.id}>
                    <TableCell className="font-medium">
                      {getStudentName(result.studentId)}
                    </TableCell>
                    <TableCell>{getExamName(result.examId)}</TableCell>
                    <TableCell>{getSubjectName(result.subject)}</TableCell>
                    <TableCell>{result.marks}/{result.maxMarks}</TableCell>
                    <TableCell>{result.percentage}%</TableCell>
                    <TableCell>
                      <Badge className={`${getGradeColor(result.grade)} text-white`}>
                        {result.grade}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <div className="flex gap-2">
                        <Button 
                          size="sm" 
                          variant="outline"
                          onClick={() => handlePrintSlip(result)}
                        >
                          Cetak Slip
                        </Button>
                        <Button 
                          size="sm" 
                          variant="destructive"
                          onClick={() => handleDeleteResult(result.id)}
                        >
                          Padam
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>

      {/* Modal untuk Slip Keputusan */}
      <Dialog open={isSlipModalOpen} onOpenChange={setIsSlipModalOpen}>
        <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>Slip Keputusan Peperiksaan</DialogTitle>
          </DialogHeader>
          {selectedResult && (
            <ResultSlip
              examResult={selectedResult}
              studentName={getStudentName(selectedResult.studentId)}
              examName={getExamName(selectedResult.examId)}
              onClose={handleCloseSlipModal}
            />
          )}
        </DialogContent>
      </Dialog>
    </div>
  )
}
